import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faCheckCircle,
  faTimesCircle,
  faCircle,
} from "@fortawesome/free-solid-svg-icons";
import hiragana from "../hiragana";

const AnsRecord = ({ isHidden, qList, correctRecord, incorrectRecord }) => {
  return (
    <table className={isHidden ? "hidden" : ""}>
      <tr>
        <th>Question</th>
        <th>Answer</th>
        <th>
          <FontAwesomeIcon icon={faCheckCircle} size="lg" />
        </th>
        <th>
          <FontAwesomeIcon icon={faTimesCircle} size="lg" />
        </th>
      </tr>
      {qList.map((q) => (
        <tr id={hiragana[q]["char_id"]}>
          <td>{hiragana[q]["character"]}</td>
          <td>{hiragana[q]["romanization"]}</td>
          <td>
            {correctRecord.includes(hiragana[q]["romanization"]) ? (
              <FontAwesomeIcon
                icon={faCircle}
                size="xs"
                className="score-correct"
              />
            ) : (
              ""
            )}
          </td>
          <td>
            {incorrectRecord.includes(hiragana[q]["romanization"]) ? (
              <FontAwesomeIcon
                icon={faCircle}
                size="xs"
                className="score-incorrect"
              />
            ) : (
              ""
            )}
          </td>
        </tr>
      ))}
    </table>
  );
};

export default AnsRecord;
